import { Colors, UpdateColorActionTypes } from './types';
import ActionTypes from './actionTypes';

const initialState: Colors = {
    rgb: { r: 0, g: 0, b: 0 },
    hsv: { h: 0, s: 0, v: 0 },
    hex: '000000',
    complementaryColor: '000000',
    approximateColors: [],
};

const colorsReducer = (
    state = initialState,
    action: UpdateColorActionTypes
): Colors => {
    switch (action.type) {
        case ActionTypes.updateRGB:
            return { ...state };
        case ActionTypes.updateHSV:
            return { ...state };
        case ActionTypes.updateComplementaryColor:
            return { ...state };
        default:
            return state;
    }
};

export default colorsReducer;
