const toRGB2 = (hsv: Array<number>) => {
    let rgb;
    const h = hsv[0] / 60;
    const s = hsv[1];
    const v = hsv[2];

    if (s === 0) {
        rgb = [v, v, v];
    } else {
        const hDegree = Math.floor(h);
        const diff = h - hDegree;
        const v1 = v * (1 - s);
        const v2 = v * (1 - s * diff);
        const v3 = v * (1 - s * (1 - diff));

        switch (hDegree) {
            case 0:
            case 6:
                rgb = [v, v3, v1];
                break;
            case 1:
                rgb = [v2, v, v1];
                break;

            case 2:
                rgb = [v1, v, v3];
                break;

            case 3:
                rgb = [v1, v2, v];
                break;

            case 4:
                rgb = [v3, v1, v];
                break;

            case 5:
                rgb = [v, v1, v2];
                break;
            default:
                rgb = [v, v, v];
        }
    }

    return rgb.map((color) => {
        return color * 255;
    });
};

export default toRGB2;
