const hexRegex: RegExp = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i;

const toRGB = (hexRGB: string) => {
    const result = hexRegex.exec(hexRGB);
    const rgb: Array<number> = [0, 0, 0];
    if (result !== null) {
        return rgb.map((_, index) => {
            // exec()は要素1以降にマッチした結果を返すためindex + 1とする。
            return parseInt(result[index + 1], 16);
        });
    }
    return rgb;
};

export const toComplementary = (hexRGB: string) => {
    const rgb: Array<number> = toRGB(hexRGB);
    const r: number = rgb[0];
    const g: number = rgb[1];
    const b: number = rgb[2];
    if (r >= 0 && r <= 255 && g >= 0 && g <= 255 && b >= 0 && b <= 255) {
        const max = Math.max(r, g, b);
        const min = Math.min(r, g, b);
        const sum = max + min;

        return rgb.map((color) => {
            return sum - color;
        });
    }
    return rgb;
};

export const toHSV = (hexRGB: string) => {
    const rgb: Array<number> = toRGB(hexRGB);
    // rgb.forEach((colors) => {
    //     if (colors < 0 && colors > 255) {
    //         return [0, 0, 0];
    //     }
    // });

    const r: number = rgb[0] / 255;
    const g: number = rgb[1] / 255;
    const b: number = rgb[2] / 255;

    const max = Math.max(r, g, b);
    const min = Math.min(r, g, b);
    const diff = max - min;

    // 色相
    let h: number;
    switch (min) {
        case max:
            h = 0;
            break;
        case b:
            h = 60 * ((g - r) / diff) + 60;
            break;
        case r:
            h = 60 * ((b - g) / diff) + 180;
            break;
        case g:
            h = 60 * ((r - b) / diff) + 300;
            break;
        default:
            h = 0;
            console.log('unexpected error.');
            break;
    }

    // 彩度
    const s = ((max - min) / max) * 100;

    // 明度
    const v = max * 100;

    return [h, s, v];
};

export default toRGB;
