import React from 'react';
import * as Strings from '../strings';
import '../assets/sass/home.scss';
import { RGBTextField } from '../components';
import HEXTextField from '../components/HEXTextField';
import ComplementaryColorArea from '../components/ComplementaryColorArea';

const rgbRegex = /^[0-9]*/;
// const hexRegex = /^[0-9a-f]{6}$/i;

const rgbValidate = (inputtedStr: string) => {
    if (rgbRegex.test(inputtedStr)) {
        const parsedInputtedStr = parseInt(inputtedStr, 10);
        console.log(`parsedInputtedStr: ${parsedInputtedStr}`);
        if (parsedInputtedStr >= 0 && parsedInputtedStr <= 255) {
            console.log('OK');
        } else {
            console.log('NG');
        }
    } else {
        console.log('NG');
    }
};

const Home: React.VFC = () => {
    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        rgbValidate(event.target.value);
    };

    return (
        <div className="home">
            <p className="home__description">
                {Strings.HOME_DESCRIPTION_PURPOSE}
            </p>
            <p className="home__description">
                {Strings.HOME_DESCRIPTION_HOW_TO_USE}
            </p>
            <p className="home__description">{Strings.HOME_TO_HEX_LABEL}</p>
            <div className="home__to-hex">
                <RGBTextField
                    id="red"
                    label={Strings.HOME_TO_HEX_INPUT_LABEL_RED}
                    handleChange={handleChange}
                />
                <RGBTextField
                    id="green"
                    label={Strings.HOME_TO_HEX_INPUT_LABEL_GREEN}
                    handleChange={handleChange}
                />
                <RGBTextField
                    id="blue"
                    label={Strings.HOME_TO_HEX_INPUT_LABEL_BLUE}
                    handleChange={handleChange}
                />
                <p className="home__arrow"> → </p>
                <HEXTextField hex="11sss0" isReadOnly />
            </div>
            <p className="home__complementary_color-label">
                {Strings.HOME_COMPLEMENTARY_COLOR_LABEL}
            </p>
            <HEXTextField hex="11sss0" isReadOnly={false} />
            <ComplementaryColorArea hex="123412" complementaryColor="aa2233" />
        </div>
    );
};

export default Home;
